<template>
	<view>
		<view class="goods-list">
			<view class="goods-item" v-for="(item,i) in goodsList" :key="i">
				<!-- 左图片 -->
				<view class="goods-item-left">
					<image :src="item.goods_small_logo"></image>
				</view>
				<!-- 右信息 -->
				<view class="goods-item-right">
					<view class="goods-info">{{item.goods_name}}</view>
					<view class="goods-price">￥{{item.goods_price}}</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		name: "my-goods-list",
		data() {
			return {
				// 请求参数
				queryObj: {
					query: '',
					cid: '',
					pagenum: 1,
					pagesize: 10
				},
				goodsList: [], //用来存储商品列表
				total: 0 //总数量，用来分页
			};
		},
		onLoad(options) {
			this.queryObj.query = options.query || ''
			this.queryObj.cid = options.cid || ''

			this.queryGoodsList()
		},
		methods: {
			async queryGoodsList() {
				const res = await uni.$request('/api/public/v1/goods/search', this.queryObj)
				console.log(res);
				const {
					goods,
					pagenum,
					total
				} = res.data.message
				if (res.statusCode !== 200) uni.$showMsg()
				else {
					this.goodsList = goods
					this.total = total
				}
			}
		}
	}
</script>

<style lang="scss">
	.goods-item {
		display: flex;
		padding: 20rpx 10rpx;
		border-bottom: 1px solid #f0f0f0;

		.goods-item-left {
			margin-right: 10rpx;

			image {
				display: block; //防止图片底下有间隙
				width: 200rpx;
				height: 200rpx;
			}
		}

		.goods-item-right {
			display: flex;
			flex-direction: column;
			justify-content: space-between;

			.goods-info {
				font-size: 26rpx;
			}

			.goods-price {
				font-size: 32rpx;
				color: #C00000;
			}
		}
	}
</style>